import React, { Component } from "react";
import { KBucketClient } from "@magland/kbucket";
import logo from "./logo.svg";
import "./App.css";

const kbclient = new KBucketClient();
kbclient.setConfig({ share_ids: ["spikeforest.spikeforest1"] });
kbclient.setPairioConfig({ collections: ["spikeforest"] });

class App extends Component {
  state = {
    response: "nothing"
  };

  componentDidMount() {
    this.fetchKBData()
      .then(res => {
        console.log(res.json);
        return;
      })
      .catch(err => console.log(err));
  }

  fetchKBData = async () => {
    let obj = await kbclient.loadObject(null, {
      key: { name: "spikeforest_studies_processed" }
    });
    if (!obj) {
      console.error("Problem loading object.");
      return;
    }
    console.log("in the fetch");
    return obj;
  };

  render() {
    return (
      <div className="App container container--lg">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1>SpikeForest</h1>
          <p>{this.state.response}</p>
          <p>
            Benchmarking of spike sorting algorithms. Seeing the spike forest
            for the trees.
          </p>
          <p>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean ut
            maximus eros. Aliquam pretium pretium tincidunt. Nam ornare leo a
            tincidunt facilisis. Nullam rutrum sagittis odio, at pulvinar sapien
            elementum et.
          </p>
          <a
            className="App-link"
            href="https://www.simonsfoundation.org/flatiron/"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn More
          </a>
        </header>
      </div>
    );
  }
}

export default App;
